    setForm(context,data) {
        return new Promise((resolve, reject) => {
            context.commit('setCurrentStep', 'finish');
            if(+state.detail.discount > 0){
                context.commit('setFormCreateField',{ name: 'discount', step: 'one', value: +state.detail.discount + 3 });
                context.commit('setFormCreateBtnState', {
                    name: 'origin',
                    value: 'normal',
                    step: 'one',
                })
                context.commit('setFormCreateBtnState', {
                    name: 'oneness',
                    value: 'normal',
                    step: 'one',
                })
            }
            context.commit('setFormCreateField',{ name: 'name', step: 'one', value: state.detail.name });
            context.commit('setFormCreateField',{ name: 'averageCheck', step: 'one', value: state.detail.averageCheckFormat });
            context.commit('setFormCreateField',{ name: 'originDiscount', step: 'one', value: state.detail.originDiscount });
            context.commit('setFormCreateField',{ name: 'discountUser', step: 'one', value: state.detail.discountUser });
            context.commit('setFormCreateField',{ name: 'onenessDiscount', step: 'one', value: state.detail.onenessDiscount });

            context.commit('setFormCreateField',{ name: 'image', step: 'two', value: state.detail.imageIcon });

            context.commit('setFormCreateField',{ name: 'aboutActionPhoto', step: 'two', value: state.detail.aboutActionPhoto });
            context.commit('setFormCreateField',{ name: 'aboutActionVideo', step: 'three', value: state.detail.aboutActionVideo });
            context.commit('setFormCreateRequired',{ name: 'aboutActionVideo', step: 'three', value: state.detail.aboutActionVideo.length > 0});
            context.commit('setFormCreateField',{ name: 'aboutActionPromo', step: 'three', value: state.detail.aboutActionPromo.replace(/(?:<br>|<br\/>|<br \/>)/g,'').replace(/&nbsp;/g,' ') });
            context.commit('setFormCreateField',{ name: 'aboutActionInclude', step: 'three', value: state.detail.aboutActionInclude.replace(/(?:<br>|<br\/>|<br \/>)/g,'').replace(/&nbsp;/g,' ')  });
            context.commit('setFormCreateRequired',{ name: 'aboutActionInclude', step: 'three', value: state.detail.aboutActionInclude.trim().length > 0});
            context.commit('setFormCreateField',{ name: 'aboutActionExclude', step: 'three', value: state.detail.aboutActionExclude.replace(/(?:<br>|<br\/>|<br \/>)/g,'').replace(/&nbsp;/g,' ')  });
            context.commit('setFormCreateRequired',{ name: 'aboutActionExclude', step: 'three', value: state.detail.aboutActionExclude.trim().length > 0});
            context.commit('setFormCreateField',{ name: 'aboutActionPrivileges', step: 'three', value: state.detail.aboutActionPrivileges.replace(/(?:<br>|<br\/>|<br \/>)/g,'').replace(/&nbsp;/g,' ')  });
            context.commit('setFormCreateRequired',{ name: 'aboutActionPrivileges', step: 'three', value: state.detail.aboutActionPrivileges.trim().length > 0});
            context.commit('setFormCreateField',{ name: 'aboutActionWarning', step: 'three', value: state.detail.aboutActionWarning.replace(/(?:<br>|<br\/>|<br \/>)/g,'').replace(/&nbsp;/g,' ')  });
            context.commit('setFormCreateRequired',{ name: 'aboutActionWarning', step: 'three', value: state.detail.aboutActionWarning.trim().length > 0});
            context.commit('setFormCreateField',{ name: 'aboutActionMore', step: 'three', value: state.detail.aboutActionMore.replace(/(?:<br>|<br\/>|<br \/>)/g,'').replace(/&nbsp;/g,' ')  });
            context.commit('setFormCreateRequired',{ name: 'aboutActionMore', step: 'three', value: state.detail.aboutActionMore.trim().length > 0});

            if(state.detail.callUsJson !== '' ){

                context.commit('setFormCreateField',{ name: 'callUs', step: 'four', value: state.detail.callUsJson });
                context.commit('setFormCreateField',{ name: 'callUsCheckBox', step: 'four', value: true });
            }
            if(state.detail.goLinkJson !== '' ){

                context.commit('setFormCreateField',{ name: 'goLink', step: 'four', value: state.detail.goLinkJson });
                context.commit('setFormCreateField',{ name: 'goLinkCheckBox', step: 'four', value: true });
            }
            if(state.detail.comeUsJson !== ''){
                context.commit('setFormCreateField',{ name: 'comeUs', step: 'four', value: state.detail.comeUsJson });
                context.commit('setFormCreateField',{ name: 'comeUsCheckBox', step: 'four', value: true });
            }

            let citiesAction = [];
            let cities = [...context.rootState.cities.items];

            for (let cityIndex in state.detail.city) {
                let city = cities.find(city => city.xmlId === state.detail.city[cityIndex]);
                if(city) {
                    citiesAction.push({
                        isChecked:  true,
                        xmlId:city.xmlId,
                        name: city.name,
                        isCapital: city.isCapital,
                    });
                }
            }
            context.commit('setFormCreateField',{ name: 'cities', step: 'five', value: citiesAction });
            context.commit('setFormCreateField',{ name: 'tags', step: 'finish', value: state.detail.tags });

            resolve();
        });
    },
    sendImageFile(context, data) {
        return new Promise((resolve, reject) => {
            if(localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined) {
                if (!state.sendForm) {
                    context.commit('setSend', true);
                    axios.post(process.env.VUE_APP_API_PATH + '/partner/actions/', data.form_data, {
                        headers: {
                            'Content-Type': 'multipart/form-data',
                            'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                        },
                        withCredentials: true,
                        params: {
                            update: 'preview',
                            actionId: data.elementId,
                        },
                        paramsSerializer: function (params) {
                            return qs.stringify(params, {arrayFormat: 'repeat'})
                        },

                    }).then(response => {
                        context.commit('setSend', false);
                        if (typeof response.data === 'object' && +response.data.error === 0) {
                            resolve(response.data.result.file);
                        } else {
                            if (+response.data.error === 401) {
                                context.dispatch('auth/logout', {}, {root: true})
                            }
                        }
                    }).catch(function (error) {
                        context.commit('setSend', false);
                        resolve();
                    });
                } else {
                    context.commit('setSend', false);
                    resolve();
                }
            } else {
                context.dispatch('auth/logout', {}, {root: true})
                context.commit('setSend', false);
                resolve();
            }
        });
    },
