                    context.dispatch('setFormCreateError',{ name: 'callUsCheckBox',step: 'four', value: true });
                    context.dispatch('setFormCreateError',{ name: 'comeUsCheckBox',step: 'four', value: true });
                    context.dispatch('setFormCreateError',{ name: 'goLinkCheckBox',step: 'four', value: true });
                    result = false;
                    if(!scrollToId) {
                        scrollToId = state.stepsCreateAction.four.fields[i].scrollTo
                    }
