    setItems(state, data) {
        if(typeof data === 'object' && data.items !== undefined) {
            Vue.set(state,'items', data.items);
        }
    },
    setMenu(state, data) {
        if(typeof data === 'object' && data.items !== undefined) {
            Vue.set(state,'menu', data.items);
        }
    },
    setLoad(state, data) {
        state.isLoad = data;
    },
    removeAction(state, data) {
        for (let itemIndex in state.items) {
            if(data.id === state.items[itemIndex].id) {
                Vue.set(state.items, itemIndex, {});
            }
        }
    },
