<template>
    <div class="wrapper-content wrapper-content-inner has-minheight" style="padding: 44px 32px 32px;">
        <div class="row-b">
            <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                <input-styled v-model="login"
                              :placeholder="'Телефон'"
                              :disabled="formAuthFields.login.disabled"
                              :isEdit="formAuthFields.login.disabled && step === 2"
                              @edit="edit"
                              name="login"
                              :is-block-arrow-phone="true"
                              @enter="enter(formAuthFields.login.name)"
                              :mask="{mask:'+7(999)999-99-99',showMaskOnHover: false}"
                              :isError="formAuthFields.login.error"
                              :errorText="formAuthFields.login.errorText"
                              @focus="removeError(formAuthFields.login.name)"
                              @input="removeError(formAuthFields.login.name)"
                              ref="loginInput"
                ></input-styled>
            </div>

            <div class="row-b__col elem-pos-fit">
                <div class="text-color-grey text-size-s" :class="{error:formAuthFields.login.error}">Допускаются только мобильные номера<br>российских операторов</div>
            </div>
        </div>


        <div class="row-b mt40" v-if="step === 2">
            <div class="row-b__col elem-pos-fix" style="width: 320px; ">
                <input-styled v-model="password"
                              name="password"
                              :placeholder="'Код из СМС'"
                              :isError="formAuthFields.password.error"
                              :errorText="formAuthFields.password.errorText"
                              @focus="removeError(formAuthFields.password.name)"
                              @enter="enter(formAuthFields.password.name)"
                              @input="removeError(formAuthFields.password.name)"
                              ref="passwordInput"
                ></input-styled>
            </div>

            <div class="row-b__col elem-pos-fit">
                <div class="text-color-grey text-size-s" v-if="timer.smsBan">Новый код отправлен на указанный номер.<br>Повторный запрос возможен через {{ timer.timeBan }} ч.</div>
                <div class="text-size-s"
                     @click="newPassword"
                     v-else-if="timer.time === 0"><span class="link-grey hover">Запросить новый</span></div>
                <div class="text-color-grey text-size-s" v-else>Новый код отправлен на указанный номер.<br>Повторный запрос возможен через {{ timer.time }} сек.</div>
            </div>
        </div>
        
        
        <div class="mt32">
            <button class="btn" @click="authorize">Продолжить</button>
        </div>
    </div>
</template>

<script>
    import InputStyled from '@/components/forms/InputStyled.vue'
    import { mapState } from 'vuex'

    export default {
        name: "Auth",
        props: {
            typeLk: {
                default: 'user'
            }
        },
        components: {
            InputStyled
        },
        computed: {
            login: {
                get () {
                    return this.formAuthFields.login.value
                },
                set (value) {
                    this.$store.commit('auth/setFormAuthField', {
                        name: this.formAuthFields.login.name,
                        value:value,
                    })
                }
            },
            password: {
                get () {
                    return this.formAuthFields.password.value
                },
                set (value) {
                    this.$store.commit('auth/setFormAuthField', {
                        name: this.formAuthFields.password.name,
                        value:value,
                    })
                }
            },
            ...mapState({
                popups: state => state.popups.items,
                formAuthFields: state => state.auth.forms.auth.fields,
                timer: state => state.auth.forms.auth.timer,
                step: state => state.auth.forms.auth.step,
                isRedirect: state => state.auth.isRedirect,
            }),
        },
        mounted() {
            let self = this;
            setTimeout(function () {
                self.inputFocus('loginInput')
            },500)
        },
        methods: {
            edit () {
                this.$store.commit('auth/setFormAuthDisabled',{
                    name: this.formAuthFields.login.name,
                    value: false
                });
                this.$store.commit('auth/setStep',{ step:1 })
                this.$nextTick(function () {
                    this.inputFocus('loginInput')
                })
            },
            enter (field) {
                this.removeError(field);
                this.authorize()
            },
            newPassword () {
                this.$store.commit('auth/setFormAuthField', {
                    name: this.formAuthFields.password.name,
                    value: '',
                });
                this.$store.dispatch('auth/checkLogin',{ resend:true });
                this.inputFocus('passwordInput')
            },
            authorize () {
                let self = this;
                switch (this.step) {
                    case 1:
                        if(!this.checkLogin()){
                            return false
                        }
                        this.$store.commit('auth/setFormAuthDisabled',{
                            name: this.formAuthFields.login.name,
                            value: true
                        });

                        this.$store.dispatch('auth/checkLogin',{ resend:false, isPartner: this.typeLk === 'partner', isLK: this.typeLk !== 'partner'}).then(function () {
                            self.$store.commit('auth/setStep',{ step:2 });
                            self.$nextTick(function () {
                                self.inputFocus('passwordInput')
                            });
                        });
                        break;
                    case 2:
                        if(!this.checkLogin() || !this.checkPassword()){
                            return false
                        }
                        this.$store.dispatch('auth/getToken',{isOrder:false}).then(() => {
                            if(this.typeLk === 'partner'){
                                this.$store.dispatch('partner/getCurrent');
                            }
                        });

                        break;
                }
            },
            inputFocus(ref) {
                if(!!this.$refs[ref]){
                    this.$refs[ref].$el.getElementsByClassName('inp-s')[0].focus()
                }
            },
            checkLogin () {
                let phone = this.formAuthFields.login.value.replace(/[^\+0-9]/g, '');
                if(phone.length < 12 || phone[2] !== '9') {
                    this.$store.commit('auth/setFormAuthError',{
                        name: this.formAuthFields.login.name,
                        value: true
                    });
                    return false;
                }
                return true;
            },
            checkPassword () {
                if(this.formAuthFields.password.value.length < 4 ) {
                    this.$store.commit('auth/setFormAuthError',{
                        name: this.formAuthFields.password.name,
                        value: true
                    });
                    return false;
                }
                return true;
            },
            removeError (field) {
                this.$store.commit('auth/setFormAuthError',{ name: field, value: false })
            },
        }
    }
</script>

