        <header v-if="$route.name!='catalogDetailImg'" class="header">
            <div class="header__top">
                <div class="inner-wrapper">
                    <div class="h-city">
                        <a  class="h-city__link" href="javascript:void(0);" @click="openCitySelect">
                            <span class="i i-marker3"></span>
                            <span class="t">{{ currentCity.name }}</span>
                        </a>
                    </div>


                    <div class="h-nav-wrap">
