                    </div>
                </div>
                <!-- end inner-wrapper -->
            </div>
            <!-- end header__top -->



            <div class="header__bottom">
                <div class="inner-wrapper">
                    <div class="h-logo">
                        <router-link class="h-logo-link" to="/">
                            <img src="@/assets/img/logo_header.svg" style="width: 163px; height: 40px; " alt="" />
                        </router-link>

                        <tooltip ref="tooltipNotifyReset"
                             :direction="'bottom-center-direction'"
                             :add-class-wrap="'h-logo__tooltip'"
                             :isBtnClose="false"
                             :add-style="{width: 175 + 'px'}">
                            <span slot="tooltipBtn"></span>

                            <div slot="tooltipContent">
                                <div class="text-center">
                                    <div>
                                        <a href="" class="i i-refresh2 i--blue"></a>
                                    </div>
                                    <div class="text-size-s" style="margin-top: 4px; ">
                                        Мы обновили сайт.<br>
                                        Чтобы все заработало –<br>
                                        <a href="">перезагрузите</a> страницу
                                    </div>
                                </div>
                            </div>
                        </tooltip>
                    </div>


                    <div class="h-right">
                        <div class="h-search-wrap">
                            <search-top></search-top>
                        </div>


                        <div class="h-menu">
                            <tooltip v-if="user.firstName"
                                 :direction="'bottom-center-direction'"
                                 :add-class-wrap="'h-menu__item'"
                                 :add-class-btn="'l'"
                                 :add-class="'h-user-menu'"
                                 :is-padding="false">
                                <span slot="tooltipBtn" @click="checkToken">
                                    <span class="i i-man5"></span>
                                <span class="t">{{ user.firstName }} {{ user.lastName[0] }}.</span>
                                </span>

                                <div slot="tooltipContent">
                                    <div class="h-user-menu__wrap">
                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-man2"></span>
                                            </span>
                                            <span class="t">{{ user.lastName }} {{ user.firstName }} {{ user.secondName }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-phone"></span>
                                            </span>
                                            <span class="t">{{ user.phone }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-mail"></span>
                                            </span>
                                            <span class="t">{{ user.email }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-marker"></span>
                                            </span>
                                            <span class="t">{{ currentCity.name }}</span>
                                        </div>
                                        <div class="h-user-menu__bottom">
                                          <button href="javascript:void(0);" class="btn btn--mini" @click="logout">
                                              <span class="i i-exit2-mini"></span>
                                              <span class="t">Выйти</span>
                                          </button>
                                          <a target="_blank" v-if="user.access_rights.is_partner" class="h-user-menu__item" href="/partner">
                                              <span class="icon">
                                                  <span class="i i-man"></span>
                                              </span>
                                              <span class="t">Кабинет партнёра</span>
                                          </a>
                                        </div>

                                    </div>
                                </div>
                            </tooltip>


                            <div class="h-menu__item" v-else>
                                <a class="l" href="javascript:void(0);" @click="openAuthorization(false)">
                                    <span class="i i-man5"></span>
                                    <span class="t">Вход/Регистрация</span>
                                </a>
                            </div>

                            <div class="h-menu__item" v-if="user.access_rights && user.access_rights.is_moderator">
                                <a class="l"
                                   target="_blank"
                                     href="/moderator/cooperation/#open_catalog">
                                    <span class="i i-book2"></span>
                                    <span class="t">Каталог</span>
                                </a>
                            </div>

                            <div class="h-menu__item" v-else>
                                <router-link class="l"
                                             active-class="active"
                                             id="h-favorite"
                                             to="/favorites">
                                    <span class="i i-star"></span>
                                    <span class="t">Избранное</span>
                                </router-link>
                            </div>

                            <div class="h-menu__item">
                                <router-link class="l"
                                             v-if="token"
                                             active-class="active"
                                             to="/personal">
                                    <span class="i i-discount3"></span>
                                    <span class="t">Мои купоны</span>
                                </router-link>
                                <a v-else href="javascript:void(0);"
                                   @click="openAuthorization(true)"
                                   class="l">
                                    <span class="i i-discount3"></span>
                                    <span class="t">Мои купоны</span>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- end inner-wrapper -->
            </div>
            <!-- end header__bottom -->

        </header>


        <main id="main">
            <router-view/>
        </main>

        <push-notify :show="popups.pushNotify.show"></push-notify>

        <div class="page-warning" v-if="cookiesWarning">
            <div class="inner-wrapper">
                <div class="page-warning__text">
			<span class="icon">
				<span class="i i-info"></span>
			</span>

                    <span class="t">Используя веб-сайт ZEMSKIDKI.RU, вы соглашаетесь на использование файлов <strong>cookies</strong>.</span>
                </div>


                <div class="page-warning__close" @click="hideCookiesWarning">
                    <span class="i i-close"></span>
                    <span class="t">Больше не показывать</span>
                </div>
            </div>
        </div>

        <vue-footer></vue-footer>
        <popup :show="popups.citySelect.show"
               :width="474"
               :add-class="'popup-city popup--styled'"
               :name="popups.citySelect.name">
            <city-select :text-note="''" :is-show-all="false"></city-select>
        </popup>


        <popup :show="popups.publicAuth.show"
               :is-set-blank="isBanned"
               :is-resize="token && !userCheck && !isBanned"
               :is-set-styled="true"
               :name="popups.publicAuth.name">
            <auth v-if="!token && !isBanned"></auth>
            <registration  v-if="token && !userCheck && !isBanned"></registration>
            <many-auth v-if="isBanned"></many-auth>
        </popup>
        <popup :show="popups.publicOffer.show"
                :width="736"
               :is-set-styled="true"
               :name="popups.publicOffer.name">
            <offer></offer>
        </popup>


        <!--<popup :show="popups.authorization.show" :name="popups.authorization.name">
            <authorization></authorization>
        </popup>-->


        <!-- пример модалки "Слишком много попыток входа"
        <popup :is-set-blank="true" :is-btn-close="true" :show="popups.manyAuth.show" :name="popups.manyAuth.name">
            <many-auth></many-auth>
        </popup> -->
    </div>
</template>




<script>
    import { mapState } from 'vuex'
    import TopMenu from '@/components/TopMenu.vue'
    import SearchTop from '@/components/SearchTop.vue'
    import Popup from '@/components/modal/Popup.vue'
    import CitySelect from '@/components/CitySelect.vue'
    import Auth from '@/components/user/auth/Auth.vue'
    import Registration from '@/components/modal/Registration.vue'
    import Authorization from '@/components/modal/Authorization.vue'
    import ManyAuth from '@/components/modal/ManyAuth.vue'
    import Tooltip from '@/components/Tooltip.vue';
    import VueFooter from '@/components/VueFooter.vue';
    import Offer from '@/views/Offer.vue';
    import PushNotify from '@/components/PushNotify.vue';

    export default {
        components: {
            TopMenu,
            SearchTop,
            Popup,
            CitySelect,
            Auth,
            Registration,
            Authorization,
            ManyAuth,
            Tooltip,
            VueFooter,
            Offer,
            PushNotify,
        },
        created() {
            this.$store.dispatch('users/getCurrent');
            this.$store.dispatch('cities/setCityCookies');
            this.$store.dispatch('actions/setFavoritesCookies');
            this.$store.dispatch('actions/setVisitedCookies');
            this.$store.dispatch('users/getCookiesWarning');
            this.$store.dispatch('cities/getCities');
            this.$store.dispatch('options/getList');
        },
        mounted() {
            let self = this;
            if (!this.isNotifyGranted() && !this.isNotifyBlocked()) {
                setTimeout(function () {
                    if (cookieHelper.readCookie('notify') === null) {
                        self.$store.commit('popups/open', {name: self.popups.pushNotify.name});
                        document.cookie = "notify=Y; max-age=86400; path=/";
                    }
                }, 5000 * 60);  // после 5 минут
            }
            this.$nextTick(function () {
                this.$el.style.display = '';
            });

            this.$store.subscribe((mutation, state) => {
                if(mutation.type=='popups/open'){
                    if(state.popups.items.notifyPageReset.show==true){
                        this.showNotifyPageReset();
                    }
                }
            });

            let scrollWidth = window.innerWidth - document.documentElement.clientWidth;
            if (scrollWidth == '0') {
                document.getElementsByTagName('body')[0].classList.add("scroll-mac-os");
            }
        },
        computed: {
            userCheck() {
                if(!!this.user.firstName && !!this.user.secondName && !!this.user.lastName && !!this.user.email){
                    return true;
                }
                return false;
            },
            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
                isBanned: state => state.auth.isBanned,
                cookiesWarning: state => state.users.cookiesWarning/* && !state.users.firstVisitRedirect*/,
                currentCity:  state => state.cities.current,
                currentCityIp:  state => state.cities.currentCityIp,
            }),
        },
        methods: {
            showPopup(popup) {
                this.$store.dispatch('popups/open',{name:popup});
            },
            hideCookiesWarning() {
                this.$store.commit('users/hideCookiesWarning');
            },
            openAuthorization: function (setIsRedirect) {
                this.$store.commit('auth/setIsRedirect', setIsRedirect);
                this.$store.dispatch('popups/open', {name: this.popups.publicAuth.name});
            },
            logout() {
                this.$store.dispatch('auth/logout');
                if(this.$route.name === 'personal'){
                    this.$router.push('/');
                }
            },
            openCitySelect() {
                this.$store.dispatch('popups/open', {name: this.popups.citySelect.name});
            },
            checkToken(){
                if(!checkToken){
                    this.logout();
                }
            },
            showNotifyPageReset(){
                this.$refs.tooltipNotifyReset.tooltipShow();
            },
            isNotifyGranted() {
                let result = false;
                if ('Notification' in window) {
                    result = Notification.permission.toLowerCase() === 'granted';
                } else {
                    result = true;
                }

                return result;
            },
            isNotifyBlocked() {
                let result = false;
                if ('Notification' in window) {
                    result = Notification.permission.toLowerCase() === 'denied';
                } else {
                    result = true;
                }

                return result;
            },
        }

    }
    function checkToken(){
        return localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined
    }
</script>


<style lang="sass">
    body
</style>
