<template>
    <div>
        <div class="popup__header">
            <div class="title">Выберите город</div>

            <!-- <div class="text-color-grey text-size-s" style="margin-top: 12px;">(Если города нет в списке – значит, мы в нём не работаем...)</div> -->
            <div v-if="textNote.length > 0" class="text-color-grey text-size-s" style="margin-top: 12px;" v-html="textNote"></div>
        </div>
        <div class="inp-s-wrap full inp-select inp-select-fastsearch inp-select--city-select" :class="{ active: showLabel}" >
          <span class="inp-s-label">Поиск по городам</span>
          <div class="search-icon">
            <span class="i i-search"></span>
          </div>
          <input type="text" v-model="citySearch" name="citySearch" class="inp-s" @focus="focus = true;" @blur="focus = false" placeholder="Москва">
          <div class="clear-btn" v-if="citySearch.length > 0" @click="citySearch = '' && isClearBtn"><span class="i i-close2"></span></div>
          <div class="select-list-shadow">
            <ul class="select-list" >
              <li v-for="city of citiesSearch" :key="city.id" class="fs-item" :class="{current: city.id === current.id}" @click="setCity(city);" v-if="!(!isShowAll && +city.count === 0)">
                  <span class="i i-ok2"></span>
                  <span v-html="highlight(city.name)" :style="{'font-weight': ((city.name.toLowerCase() === 'москва' || city.name.toLowerCase() === 'санкт-петербург') && city.id !== current.id)?'bold':''}"></span>
              </li>
            </ul>
            <ul class="select-list --ghost" v-show="false">
              <li>
                <div></div>
              </li>
              <li>
                <div></div>
              </li>
              <li>
                <div></div>
              </li>
            </ul>
          </div>
        </div>
        <div class="city-not-found" v-if="!citiesSearch.length">
          <svg class="big-icon">
            <use xlink:href="@/assets/img/big-icons-sprite.svg#emoji-ufo"></use>
          </svg>
          <div class="text-size-l text-center text-bold text-color">
             Если города нет в списке,<br>значит мы в нём не работаем
          </div>
        </div>
    </div>
</template>


<script>
    import { mapState } from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue'

    export default {
        name: "CitySelect",
        components: {
            InputStyled
        },
        props: {
            isShowAll: {
               default() {
                   return true;
               }
            },
            isSet: { // если true, то меняем город пользователя, иначе просто отправлем событием @set.city
                default() {
                    return true;
                }
            },
            textNote: {
                default() {
                    return '(Если города нет в списке – значит, там пока нет скидок...)';
                }
            },
            propCurrent: {
                default() {
                    return false;
                }
            },
        },
        data() {
            return {
                citySearch: '',
                choosen: null,
                focus: false,
            }
        },
        computed: {
            showLabel() {
                return this.focus || this.citySearch.length > 0;
            },
            citiesSearch(){
                let cities;
                if(this.citySearch.length > 0 && this.cities.size > 0) {
                    cities = [...this.cities].filter(city => city.name.toLowerCase().indexOf(this.citySearch.toLowerCase()) === 0);
                } else {
                    cities =  [...this.cities];
                }
                let newIndex = 0;
                for(let city of cities){
                    if(+city.id === +this.current.id && !(!this.isShowAll && +city.count === 0)){
                        let index = cities.indexOf(city);
                        if(index !== -1) {
                            cities.splice(newIndex, 0, cities.splice(cities.indexOf(city), 1)[0]);
                            if(city.name.toLowerCase() !== 'москва'){
                                newIndex = 1;
                            }
                        }
                    }
                }
                for(let city of cities){
                    if(city.name.toLowerCase() === 'москва' || city.name.toLowerCase() === 'санкт-петербург' || city.name.toLowerCase() === 'подольск'){
                        if(city.name.toLowerCase() === 'санкт-петербург' && +city.id !== +this.current.id){
                            let index = cities.indexOf(city);
                            if(index !== -1) {
                                cities.splice(newIndex+1, 0, cities.splice(cities.indexOf(city), 1)[0]);
                            }
                        }
                        if(city.name.toLowerCase() === 'москва' && +city.id !== +this.current.id){
                            let index = cities.indexOf(city);
                            if(index !== -1) {
                                cities.splice(newIndex, 0, cities.splice(cities.indexOf(city), 1)[0]);
                            }
                        }
                    }
                }
                return cities;
            },
            current() {
                if(this.propCurrent) {
                    return this.propCurrent
                } else {
                    return this.currentCityState
                }
            },
            ...mapState({
                popups: state => state.popups.items,
                cities: state => state.cities.items,
                currentCityState: state => state.cities.current,
            }),
        },
        methods: {
          highlight: function(text) {
              let txt = text + '';
              return txt.replace(new RegExp(this.citySearch, 'gi'), '<span class="highlighted">$&</span>');
          },
            setCity: function (city) {
                if(this.isSet){
                    this.$store.dispatch('cities/setCity', {city: city,closePopup: this.popups.citySelect.name,currentSection: this.$route.params.sid}).then(()=>{
                        if(this.$route.path == '/'){
                            this.$store.dispatch('actions/getCouponList', {currentSection: ''}, { root: true });
                        }
                        else{
                            this.$router.push('/');
                        }
                    });
                } else {
                    this.$emit('set:city', city);
                }
            }
        }
    }
</script>

<style scoped>

</style>
