<template>
  <transition name="fade-popup">
    <div class="push-notify" v-if="show">
      <img src="@/assets/img/for-sprite/notification.svg" alt="">
      <div class="text">
        Включите уведомления, чтобы узнать<br>о новых скидках в своём городе
      </div>
      <div class="button-block">
        <button class="btn" @click="apply">Включить</button>
        <button class="btn btn--white" @click="close">Не сейчас</button>
      </div>
    </div>
  </transition>
</template>

<script>
    import { mapState } from 'vuex'
    export default {
        name: "PushNotify",
        data: function() {
            return {
            }
        },
        props: {
          show: {
            default: function(){
              return false
            }
          }

        },
        computed: {
          ...mapState({
              popups: state => state.popups.items,
          }),
        },
        methods: {
          close: function(){
            this.$store.commit('popups/close', {name: this.popups.pushNotify.name});
          },
          apply: function () {
            window.notify_subscribe();
            this.close();
          }
        },
    }


</script>

<style scoped>
    .fade-popup-enter-active, .fade-popup-leave-active {
        transition: all .15s;
        transform: translateX(0);
        opacity: 1;
    }
    .fade-popup-enter, .fade-popup-leave-to {
        opacity: 0;
        transform: translateX(180px);
    }
</style>
