<template>
    <div class="h-search">
        <input v-model="searchQuery"
               @keyup.enter = "getSearch()"
               ref="searchInput"
               type="text"
               placeholder="Поиск скидок"
               value="">
               
        <span @click="resetSearch()" class="h-search__clear i i-close2"></span>
        
        <div class="submit" @click="getSearch">
            <span class="i i-search"></span>
        </div>
    </div>
</template>

<script>

    import { mapState } from 'vuex'

    export default {
        name: "SearchTop",
        computed: {
            searchQuery: {
                get () {
                    return this.$store.state.search.query
                },
                set (value) {
                    this.$store.commit('search/updateQuery', value)
                }
            },
            ...mapState({
            }),
        },
        methods: {
            getSearch () {
                this.$store.commit('search/updateQueryUrlPrevious', {url: this.$route.path});
                if(this.$store.state.search.query.length > 0) {
                    if (this.$router.history.current.path !== '/search') {
                        this.$router.push('/search');
                    }
                    this.$store.dispatch('actions/initialSearchState');
                    this.$store.dispatch('actions/getCouponList', {currentSection: false, isSearch: true})
                }
            },
            resetSearch() {
                this.searchQuery = '';
                this.$refs.searchInput.focus();
            },
        }
    }
</script>

