<template>
    <transition name="fade-top-menu">
        <div class="h-nav">
            <div class="h-nav-item" v-for="item in menuList" :key="item.link">
                <router-link class="h-nav-item-link h-link" :to="item.link" :class="{'flyToWork': item.link == '/partner/coupons-work/'}">
                    <span>{{ item.name }}</span>
                    <span v-if="isCounterShow(item) && routerCode != item.code" :class="counterColor(item)" class="num">!</span>
                </router-link>
            </div>
        </div>
    </transition>
</template>

<script>
    import { mapState } from 'vuex'
    export default {
        name: "TopMenu",
        props: {
            type: {
                default() {
                    return 'public'
                }
            }
        },
        created () {

            if (this.type === 'public') {
                //на публичке сразу показываем меню, но запросом всё равно получаем актуальное меню.
                //если меняется меню на стороне api, то надо в pub_menu вставить актуальный объект
                let pub_menu = {"error":"0","result":{"method":"GET","top":[{"name":"\u041e \u043f\u0440\u043e\u0435\u043a\u0442\u0435","link":"\/about\/","iconMobile":"info","hideMobile":false},{"name":"\u0421\u0442\u043e\u0438\u043c\u043e\u0441\u0442\u044c","link":"\/deposit\/","iconMobile":"beaker","hideMobile":true},{"name":"\u0420\u0435\u0439\u0442\u0438\u043d\u0433","link":"\/rating\/","iconMobile":"beaker","hideMobile":false},{"name":"\u0421\u0442\u0430\u0442\u0438\u0441\u0442\u0438\u043a\u0430","link":"\/statistic\/","iconMobile":"stats","hideMobile":false},{"name":"\u0421\u043e\u0442\u0440\u0443\u0434\u043d\u0438\u0447\u0435\u0441\u0442\u0432\u043e","link":"\/cooperation\/","iconMobile":"coop","hideMobile":false},{"name":"\u0412\u043e\u043f\u0440\u043e\u0441\u044b \u0438 \u043e\u0442\u0432\u0435\u0442\u044b","link":"\/faq\/","iconMobile":"faq","hideMobile":false},{"name":"\u041a\u043e\u043d\u0442\u0430\u043a\u0442\u043d\u0430\u044f \u0438\u043d\u0444\u043e\u0440\u043c\u0430\u0446\u0438\u044f","link":"\/contacts\/","iconMobile":"contact-info","hideMobile":false}]},"version":"1.2.5"};
                this.$store.commit('menus/setMenu', pub_menu.result);
            }

            this.$store.dispatch('menus/getMenu',{type:this.type}).then((response)=>{
                if(this.type=='partner') {
                    let menu = [];
                    for (let i in response.top) {
                        switch (response.top[i].link) {
                            case '/partner/statistic/':
                                if (this.user.permission.indexOf(9) !== -1) {
                                    menu.push(response.top[i]);
                                }
                                break;
                            case '/partner/commission/':
                                if (this.user.permission.indexOf(9) !== -1) {
                                    menu.push(response.top[i]);
                                }
                                break;
                            case '/partner/opinion/':
                                break;
                            default:
                                menu.push(response.top[i]);
                                break;
                        }
                    }
                    this.$store.commit('menus/setMenu', {top: menu});
                } else if(this.type=='moderator' && this.user.permissionModerator.length > 0){
                    let menu = [];
                    for (let i in response.top) {
                        switch (response.top[i].link) {
                            case '/moderator/repayment/':
                                if (this.user.permissionModerator.indexOf(19) !== -1) {
                                    menu.push(response.top[i]);
                                }
                                break;
                            default:
                                break;
                        }
                    }
                    this.$store.commit('menus/setMenu', {top: menu});
                } else {
                    this.$store.commit('menus/setMenu', response);
                }
            });
            if(this.type=='moderator'){
                this.$store.dispatch('moderatorCounters/getItems',{type:this.type});
                setInterval(()=>{
                    this.$store.dispatch('moderatorCounters/getItems',{type:this.type});
                }, 30000);
            }
            if(this.type=='partner'){
                this.$store.dispatch('partnerCounters/getItems',{type:this.type});
                setInterval(()=>{
                    this.$store.dispatch('partnerCounters/getItems',{type:this.type});
                }, 30000);
            }
        },
        computed: {
            ...mapState({
                menuList: state => state.menus.itemsTop,
                counters: state => state.moderatorCounters.items,
                countersPartner: state => state.partnerCounters.items,
                user: state => state.users.user,
            }),
            routerCode(){
                return this.$route.name ? this.$route.name.split('-')[0] : 'moderator';
            }
        },
        methods:{
            isCounterShow: function(item){
                let result = false;
                if(this.type=='moderator' ){
                    if(this.counters[item.code]){
                        for (let itemIndex in this.counters[item.code]) {
                            if(this.counters[item.code][itemIndex]){
                                return true;
                            }
                        }
                    }
                }
                if(this.type=='partner' ){
                    if(this.countersPartner[item.code]){
                        for (let itemIndex in this.countersPartner[item.code]) {
                            if(this.countersPartner[item.code][itemIndex]){
                                return true;
                            }
                        }
                    }
                }
                return result;
            },
            counterColor: function(item){
                let result = '';
                if(this.type=='moderator' ){
                    if(this.counters[item.code]){
                        for (let itemIndex in this.counters[item.code]) {
                            if(this.counters[item.code][itemIndex] === 'orange'){
                                return '--orange';
                            }
                        }
                    }
                }
                if(this.type=='partner' ){
                    if(this.countersPartner[item.code]){
                        for (let itemIndex in this.countersPartner[item.code]) {
                            if(this.countersPartner[item.code][itemIndex] === 'orange'){
                                return '--orange';
                            }
                        }
                    }
                }
                return result;
            }
        }
    }
</script>

<style scoped>
    .fade-top-menu-enter-active, .fade-top-menu-leave-active {
        transition: opacity .3s;
    }
    .fade-top-menu-enter, .fade-top-menu-leave-to {
        opacity: 0;
    }
</style>
